:root {
  --main-color: #29b0ee;
  --text: #121212;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#root {
  width: 100%;
  height: 100vh;
  background-color: #29b0ee;
  background-color: var(--main-color);
}
.ccl-btn {
    outline: none;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    font-weight: 400;
    font-size: 24px;
    color: #fff;
    background-color: var(--main-color);
    box-shadow: 0 0 2px rgb(4, 122, 151),
        1px 1px 1px rgba(0,0,0,.1);
    -webkit-user-select: none;
            user-select: none;
    position: relative;
    overflow: hidden;
    z-index: 2;

    font-family: Cambria;
}
.cal-box-circle {
    background-color: rgba(0, 0, 0, .1);
    position: absolute;
    -webkit-animation: circleIN 0.8s ease-out forwards;
            animation: circleIN 0.8s ease-out forwards;
    -webkit-transform-origin: right top;
            transform-origin: right top;
    border-radius: 50%;
    z-index: -1;
}
@-webkit-keyframes circleIN {
    from {
        height: 0;
        width: 0;
    }
    to {
        height: 280px;
        width: 280px;
    }
}
@keyframes circleIN {
    from {
        height: 0;
        width: 0;
    }
    to {
        height: 280px;
        width: 280px;
    }
}
.right-menu {
    background-color: #f6f6f6;
    position: absolute;
    border-radius: 5px;
    z-index: 1000;
    padding: 10px;
}

.Alert_alert__1fElv {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    font-size: 1rem;
}
.Alert_alertContent__YZpa4 {
    width: clamp(200px, 17rem, 500px);
    background-color: var(--main-color);
    height: 26px;
    margin: 20px auto;
    line-height: 26px;
    border-radius: 5px;
    padding: 5px 10px;
    box-shadow: 14px 14px 38px #2396ca, -10px -10px 20px #2fcaff;
    display: flex;
    align-items: center;
    justify-content: center;
}


/* 新拟态样式风格 */
.calculator-container {
    width: 100%;
    height: 100%;
    display: flex;
}
.calculator-inner-box {
    width: 335px;
    height: 450px;
    box-sizing: border-box;
    padding: 10px;
    background-color: var(--main-color);
    margin: 0 auto;
    align-self: center;
    border-radius: 10px;
    box-shadow:  14px 14px 38px #2396ca,
             -14px -14px 38px #2fcaff;
}
.calculator-grid-box {
    display: grid;
    height: 100%;
    grid-template-columns: repeat(4, 75px);
    grid-template-rows: 20px 50px repeat(6, 50px);
    align-content: end;
    grid-gap: 5px; 
    grid-gap: 5px; 
    gap: 5px;
}
.stage,
.show  {
    grid-column: span 4;
    place-self: end;
    color: white;
    max-width: 100%;
    overflow: auto;
}
.show {
    font-size: 45px;
}
.show::-webkit-scrollbar {
    height: 6px;
    width: 6px;
}
.show::-webkit-scrollbar-thumb {
    height: 6px;
    background-color: rgba(0, 0, 0, .3);
    border-radius: 5px;
}

.zero {
    grid-column: span 2;
}
.division, .multi,
.sub, .add {
    background: rgb(48, 237, 41);
}
.equal {
    background: darkcyan;
}
.ac, .clear-one {
    background: rgb(221, 37, 37);
}
.clear-one {
    font-size: 14px;
    /* border-radius: 100% 0 0 100%; */
}
.nega, .percent, .cent2, .square {
    background-color: rgb(199, 124, 25);
}


/* ^ alert-box ^ */
#alert-box aside {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    transition: -webkit-transform 0.4s cubic-bezier(0, 0, 0.33, 1.21);
    transition: transform 0.4s cubic-bezier(0, 0, 0.33, 1.21);
    transition: transform 0.4s cubic-bezier(0, 0, 0.33, 1.21), -webkit-transform 0.4s cubic-bezier(0, 0, 0.33, 1.21);
}

